import { 
  Button,
  Tabbar,
  TabbarItem,
  Form,
  Field,
  Popup,
  Cell,
  Loading,
  Toast,
  Icon,
  ActionSheet,
  NavBar,
  CellGroup,
  Tab,
  Tabs,
  Swipe,
  SwipeItem,
  Slider,
  Grid,
  GridItem,
  List,
  PullRefresh,
  Empty,
  Search
} from 'vant';
const vants = [
  Button,
  Tabbar,
  TabbarItem,
  Form,
  Field,
  Popup,
  Cell,
  Loading,
  Toast,
  Icon,
  ActionSheet,
  NavBar,
  CellGroup,
  Tab,
  Tabs,
  Swipe,
  SwipeItem,
  Slider,
  Grid,
  GridItem,
  List,
  PullRefresh,
  Empty,
  Search 
]
export default {
  install(Vue) {
    vants.forEach(Component => {
      Vue.use(Component)
    })
  }
}