import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    musicData:'',
    video:[],
    musictime:0,
    isplay:false,
    musicids:[],
  },
  mutations: {
    //歌曲基本信息
    setmusicid(state, musicData) {
      state.musicData = musicData
    },
    //设置播放表单
    setplaymusicids(state,playmusicids){
      state.musicids = playmusicids
    }
  },
  actions: {
  },
  modules: {
  }
})
