<template>
  <div class="lyric">
    <div class="box" v-if="open">
      <div class="songbox">
        <ul :style="{ top: -settop + 'px' }">
          <li
            v-for="(item, index) in sortmusiclyric"
            @click="toggle(item, index)"
            :class="{
              active: index == newIndex,
              opacitystyle1: index == newIndex - 1 || index == newIndex + 1,
              opacitystyle2: index == newIndex - 2 || index == newIndex + 2,
              opacitystyle3: index == newIndex - 3 || index == newIndex + 3,
            }"
            ref="list"
            :key="index"
            :style="{ opacitystyle1: index == newIndex - 1 }"
          >
            {{ item.text != "" ? item.text : "&nbsp;" }}
          </li>
        </ul>
      </div>
      <div class="bg-box"></div>
    </div>
    <div v-else class="nolyric">暂无歌词...</div>
  </div>
</template>

<script>
import "../assets/less/lyric.less";

export default {
  props:['musicid','currentTimedata'],
  data() {
    return {
      settop: 0,
      songData: [],
      newIndex: 0,
      songtime: 0,
      seekValue: "",
      lyric: "",
      open:false
    };
  },
  created() {
    this.seekMusic(this.musicid);
  },
  computed: {
    songarr() {
      let arr = this.lyric.split(/\n/);
      // 去除最后一个空元素
      if (arr[arr.length - 1] == "") {
        arr.pop();
      }
      // 正则匹配
      arr.map((v) => {
        /\[(\d+):(\d+\.\d+)\]\[?(\d+)?:?(\d+\.\d+)?\]?(.+)?/.test(v);

        if (RegExp.$5) {
          if (RegExp.$3 != "") {
            this.songData = [
              ...this.songData,
              {
                time: parseInt(RegExp.$1) * 60 + parseFloat(RegExp.$2),
                text: RegExp.$5,
              },
              {
                time: parseInt(RegExp.$3) * 60 + parseFloat(RegExp.$4),
                text: RegExp.$5,
              },
            ];
          } else {
            this.songData = [
              ...this.songData,
              {
                time: parseInt(RegExp.$1) * 60 + parseFloat(RegExp.$2),
                text: RegExp.$5,
              },
            ];
          }
        }
      });
      return this.songData;
    },
    //排序歌词
    sortmusiclyric() {
      return [...this.songarr].sort((p1, p2) => {
        return p1.time - p2.time; // 升序
      });
    },
  },
  watch: {
    currentTimedata(val, newval) {
      if (val != newval) {
        for (let i = 0; i < this.sortmusiclyric.length; i++) {
          if (i == this.sortmusiclyric.length - 1) {
            return (this.newIndex = i);
          }
          if (
            this.sortmusiclyric[i].time <= this.currentTimedata &&
            this.currentTimedata <= this.sortmusiclyric[i + 1].time
          ) {
            return (this.newIndex = i);
          }
        }
      }
    },
    musicid(val, newval) {
      if (val != newval) {
        this.seekMusic(val);
      }
    },
    newIndex(val, newval) {
      if (val != newval) {
        this.settop = this.$refs.list[newval].offsetTop;
      }
    },
  },
  methods: {
    //获取歌词详情
    seekMusic(musicid) {
      this.axios({
        method: "GET",
        url: "/lyric",
        params: {
          id: musicid,
        },
      })
        .then((result) => {
          if (result.data.code === 200) {
            if (result.data.lrc) {
              this.open = true
              this.songData = [];
              this.lyric = result.data.lrc.lyric;
            }else{
              this.open = false
            }
            
          }
        })
        .catch((err) => {
          console.error(err)
        });
    },
    //点击跳跃播放
    toggle(item, index) {
      this.$emit("set-currentTime", item.time)
      this.newIndex = index;
    },
  },
};
</script>