<template>
  <div>
    <div class="pymusic" @click="goPlayPage">
      <div class="pymusic-box fl" :class="getisplay ? 'run' : 'stop'">
        <img class="auto-img" :src="musicpicUrl" alt="" />
      </div>
      <div class="pymusic-contnet fl">
        <div class="pymusic-title">{{musicname}}</div>
        <div class="pymusic-name"><span
                  style="margin: 0 2px"
                  v-for="(item, index) in singer"
                  :key="index"
                  >{{ item.name }}</span
                ></div>
      </div>
      <div class="pymusic-operation fr">
        <div class="pymusic-play" @click.stop="taggerPlayMusic">
          <img
            v-show="!getisplay"
            src="../assets/images/mv-play.png"
            class="auto-img"
          />
          <img
            v-show="getisplay"
            src="../assets/images/mv-pause.png"
            class="auto-img"
          />
        </div>
      </div>
      <audio
          :style="{ opacity: 0 }"
          ref="audio"
          @timeupdate="getmusictime"
          controls
          autoplay
          :src="musicurl"
        ></audio>
    </div>
    <van-popup
      class="mdetail"
      v-model="isshowdetail"
      position="bottom"
      :style="{ height: '100%', width: '100%' }"
    >

      <div class="detail">
        <transition name="van-slide-down">
          <div v-show="visible">Slide Down</div>
        </transition>
        <div class="detail-bgimg-box">
          <img class="detail-bgimg" :src="musicpicUrl" />
        </div>
        <div class="detail-tab">
          <van-nav-bar left-arrow>
            <template #left>
              <van-icon name="arrow-left" @click="goback" />
            </template>
            <template #title>
              <div class="musicname">{{ musicname }}</div>
              <div class="singer">
                <span
                  style="margin: 0 2px"
                  v-for="(item, index) in singer"
                  :key="index"
                  >{{ item.name }}</span
                >
              </div>
            </template>
            <template #right>
              <van-icon name="ellipsis" />
            </template>
          </van-nav-bar>
        </div>
        <div class="detail-content">
          <!-- <div class="detail-img-box" :class="getisplay ? 'run' : 'stop'"> -->
          <div class="detail-img-box" :class="getisplay ? 'run' : 'stop'">
            <img
              class="detail-img-bgimg"
              src="../assets/images/55f2bd88558d452b4fd208f9d51783df.png"
            />
            <img class="auto-img detail-img" :src="musicpicUrl" />
          </div>
          <div class="detail-lyric">
            <Lyric
              :musicid="getmusicid"
              :currentTimedata="currentTimedata"
              @set-currentTime="setcurrentTime"
            ></Lyric>
          </div>
        </div>

        
        <div class="detail-control">
          <div class="detail-type-box">
            <img
              class="auto-img"
              @click="getMusicLike"
              :src="islike ? operation.activeUrl : operation.Url"
            />
            <img class="auto-img" src="../assets/images/download.png" alt="" />

            <img
              class="auto-img"
              badge="5"
              src="../assets/images/information.png"
              alt=""
            />
            <img class="auto-img" src="../assets/images/volume.png" alt="" />
            <van-icon color="#dbdbdb" name="ellipsis" />
          </div>

          <div class="detail-time-box">
            <van-slider
              class="detail-slider"
              bar-height="2"
              max="100"
              min="0"
              v-model="value"
              @drag-start="onChange"
              @drag-end="dragEnd(value)"
            />
            <div class="detail-time">
              <span class="bigintime">{{ currentTimedata | timeFormat }}</span>
              <span class="totaltime">{{ durationdata | timeFormat }}</span>
            </div>
          </div>

          <div class="detail-control-box">
            <img
              @click="taggerPlayType"
              class="auto-img"
              :src="musicplaytype"
            />
            <img
              class="auto-img"
              @click="prevplay"
              src="../assets/images/prev.png"
            />
            <div class="detail-play-img" @click="taggerPlayMusic">
              <img class="auto-img" :src="isplay" />
            </div>
            <img
              class="auto-img"
              @click="nextplay"
              src="../assets/images/next.png"
            />
            <!-- <van-cell  @click="showPopup"> -->
            <img
              class="auto-img"
              is-link
              @click="getplaymusic"
              src="../assets/images/musicmenu.png"
            />
            <!-- </van-cell> -->
            <van-popup
              class="playmusic"
              v-model="show"
              round
              position="bottom"
              :style="{ height: '50%' }"
            >
              <div class="showtop">
                <div>
                  当前播放<span>{{ songListsData.length }}首</span>
                </div>
                <div>
                  <van-icon @click.stop="playAllMusic" name="play-circle-o" />
                </div>
              </div>
              <div class="showitembox">
                <div
                  class="showitem"
                  v-for="(item, index) in songListsData"
                  :key="index"
                  @click="setStateCount(item)"
                >
                  <div class="show-title-box van-ellipsis">
                    <div
                      class="play-icon"
                      :class="{ 'play-icon-active': playmusicindex == index }"
                    >
                      <van-icon name="volume" />
                    </div>
                    <div class="musicname">{{ item.name }}</div>
                    <div class="l">-</div>
                    <div class="showname">
                      <span v-for="(ite, ind) in item.ar" :key="ind">{{
                        ite.name
                      }}</span>
                    </div>
                  </div>
                  <div class="delite"><van-icon name="cross" /></div>
                </div>
              </div>
            </van-popup>
            <!-- songListsData -->
          </div>
        </div>
      </div>
    </van-popup>
  </div>
</template>

<script>
import "../assets/less/playmusic.less";
import "../assets/less/musicdetail.less";
import Lyric from "../components/Lyric";
import { setupCookie } from "../assets/js/setupCookie";
export default {
  data() {
    return {
      // audio:this.$refs.audio,
      // isplay: true,
      isshowdetail: false,
      // stopRotate: 0,
      // timer: "",
      //音乐时长
      currentTimedata: 0,
      durationdata: 0,
      //音乐路径
      musicurl: "",
      //旋转
      getisplay: true,
      //播放模式
      playtype: 0,
      isplay: require("../assets/images/mv-pause.png"),
      //滑块
      value: 0,
      //请求类型
      type: 0,
      // musicId: "",
      //歌曲头部信息
      musicname: "",
      singer: "",
      audio: "",
      //歌曲封面
      musicpicUrl: "",
      //当前歌曲播放下标
      playmusicindex: 0,
      //歌曲评价条数
      reviewscount: 0,
      //当前播放歌单
      songListsData: [],
      slider: true,
      islike: false,
      show: false,
      aa: false,
      //音乐id
      musicid: 0,
      visible:false,
      //当前歌单id数组
      // getplaymusicids: [],
      //收藏tab
      operation: {
        Url: require("../assets/images/love.png"),
        activeUrl: require("../assets/images/love-active.png"),
      },
      // 1477539203
    };
  },
  filters: {
    timeFormat: function (value) {
      // value = value == 'NaN' ? 0 : value
      value = isNaN(value) ? 0 : value;
      //播放量数值处理
      let m =
        Math.floor(value / 60) >= 10
          ? Math.floor(value / 60)
          : "0" + Math.floor(value / 60);
      let s =
        Math.floor(value % 60) >= 10
          ? Math.floor(value % 60)
          : "0" + Math.floor(value % 60);
      return `${m}:${s}`;
    },
  },
  created() {
    if (this.getmusicid) {
      
    //获取歌曲详情
    this.getMusicDetails(this.getmusicid);
    //获取歌曲评论
    this.getmusic(this.getmusicid);
    //判断是否已收藏
    this.getMusicIsLike();
    //获取歌曲url
    this.getChechMusic(this.getmusicid);

    // 获取歌曲是否喜欢收藏
    this.getMusicIsLike();
    //获取热歌表单
    // this.setmusicids();
    ////获取当前播放第几首
    this.getplaymusicindex();

    }

  },
  computed: {
    getmusicid(){
      return this.$store.state.musicData
    },
    getplaymusicids(){
      return this.$store.state.musicids
    },
    musicplaytype() {
      if (this.playtype == 0) {
        return require("../assets/images/type_0.png");
      } else if (this.playtype == 1) {
        return require("../assets/images/type_1.png");
      } else if (this.playtype == 2) {
        return require("../assets/images/type_2.png");
      } else {
        return require("../assets/images/type_3.png");
      }
    },
  },
  watch: {
    getplaymusicids(val,oval){
      if (val != oval) {
        this.getallmusicdata()
      }
    },
    getmusicid(val,oval){
      if (val != oval) {
         //获取歌曲详情
        this.musicid = val
       // this.getMusicDetails(val);
        // //获取歌曲评论
        // this.getmusic(val);
        // //判断是否已收藏
        // this.getMusicIsLike();
        // //获取歌曲url
        // this.getChechMusic(val);
      }
    },
    musicid(val,oval){
      if (val != oval) {
        //获取歌曲详情
        this.getMusicDetails(val);
        //获取歌曲评论
        this.getmusic(val);
        //判断是否已收藏
        this.getMusicIsLike();
        //获取歌曲url
        this.getChechMusic(val);
      }
    }
  },
  methods: {
    //前往详情页面
    goPlayPage() {
      this.isshowdetail = true;
    },
    //返回上一页
    goback() {
      this.isshowdetail = false;
    },
    //拉动进度条
    dragEnd(value) {
      this.slider = true;
      this.$refs.audio.currentTime = (value / 100) * this.$refs.audio.duration;
    },
    // 关闭滑块赋值
    onChange() {
      this.slider = false;
    },
    //歌词设置时长
    setcurrentTime(time) {
      this.$refs.audio.currentTime = time;
    },
    // 切换播放模式
    taggerPlayType() {
      this.playtype = ++this.playtype >= 4 ? 0 : this.playtype;
    },

    //监听时长变化
    getmusictime(e) {
      this.durationdata = e.target.duration;
      this.currentTimedata = e.target.currentTime;
      if (this.slider) {
        this.value = (e.target.currentTime / e.target.duration) * 100;
      }
      if (e.target.paused) {
        this.isplay = require("../assets/images/mv-play.png");
        this.getisplay = false;
      } else {
        this.isplay = require("../assets/images/mv-pause.png");
        this.getisplay = true;
      }
      if (e.target.ended) {
        if (this.playtype == 1) {
          this.$refs.audio.play();
        } else {
          this.nextplay();
        }
      }
    },
    // 播放、暂停
    taggerPlayMusic() {
      if (this.$refs.audio.paused) {
        this.$refs.audio.play();
        this.getisplay = true;
        this.isplay = require("../assets/images/mv-pause.png");
      } else {
        this.isplay = require("../assets/images/mv-play.png");
        this.getisplay = false;
        this.$refs.audio.pause();
      }
    },
    //获取当前播放第几首
    getplaymusicindex() {
      this.playmusicindex = this.getplaymusicids.indexOf(+this.musicid);
    },

    //获取歌曲详情
    // 1481592204
    getMusicDetails(id) {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "GET",
        // 获取歌单详情
        url: "/song/detail",
        params: {
          ids: id,
        },
      })
        .then((result) => {
          this.$toast.clear();
          if (result.data.code === 200) {
            this.musicname = result.data.songs[0].name;
            this.singer = result.data.songs[0].ar;
            this.musicpicUrl = result.data.songs[0].al.picUrl;
          }
        })
        .catch((err) => {
          this.$toast.clear();
          console.error(err)
        });
    },
    //获取歌曲url
    getChechMusic(mid) {
      this.axios({
        method: "GET",
        // 获取歌单详情
        url: "/check/music",
        params: {
          id: mid,
        },
      })
        .then((result) => {
          if (result.data.success) {
            this.axios({
              method: "GET",
              // 获取歌单详情
              url: "/song/url",
              params: {
              id: mid,
            },
            })
            .then((result) => {
              if (result.data.code == 200) {
                this.musicurl = result.data.data[0].url;
              }
            })
            .catch((err) => {
              console.error(err)
            });
          }else{
            this.visible = true
          }
        })
        .catch((err) => {
          console.error(err)
        });

      
    },
    //喜欢歌曲
    // 1481592204
    getMusicLike() {
      this.axios({
        method: "GET",
        // 获取歌单详情
        url: "/like",
        params: {
          like: !this.islike,
          id: this.getmusicid,
          cookie: setupCookie.getCookies(),
        },
      })
        .then((result) => {
          if (result.data.code === 200) {
            this.islike = !this.islike;
          }
        })
        .catch((err) => {
          console.error(err)
        });
    },
    //获取歌曲是否喜欢收藏
    getMusicIsLike() {
      this.axios({
        method: "GET",
        url: "/likelist",
        params: {
          uid: localStorage.getItem("USERID"),
          cookie: setupCookie.getCookies(),
        },
      })
        .then((result) => {
          if (result.data.code === 200) {
            let musicidsdata = result.data.ids;
            if (musicidsdata.indexOf(this.getmusicid) >= 0) {
              this.islike = true;
            } else {
              this.islike = false;
            }
          }
        })
        .catch((err) => {
          console.error(err)
        });
    },
    //下一曲
    nextplay() {
      let musicindex = this.getplaymusicids.indexOf(+this.musicid);

      if (this.playtype == 0) {
        this.playmusicindex = musicindex + 1;
        if (this.playmusicindex >= this.getplaymusicids.length) {
          this.playmusicindex = 0;
        }
      } else if (this.playtype == 2 || this.playtype == 1) {
        this.playmusicindex = musicindex + 1;
        if (this.playmusicindex >= this.getplaymusicids.length) {
          this.playmusicindex = this.getplaymusicids.length - 1;
        }
      } else {
        let rumnab = Math.floor(Math.random() * this.getplaymusicids.length);
        while (rumnab == this.playmusicindex) {
          rumnab = Math.floor(Math.random() * this.getplaymusicids.length);
        }
        this.playmusicindex = rumnab;
      }
      this.$refs.audio.play();
      this.musicid = this.getplaymusicids[this.playmusicindex];
    },
    //上一曲
    prevplay() {
      let musicindex = this.getplaymusicids.indexOf(+this.musicid);
      if (this.playtype == 0) {
        this.playmusicindex = musicindex - 1;
        if (this.playmusicindex < 0) {
          this.playmusicindex = this.getplaymusicids.length - 1;
        }
      } else if (this.playtype == 2 || this.playtype == 1) {
        this.playmusicindex = musicindex - 1;
        if (this.playmusicindex < 0) {
          this.playmusicindex = 0;
        }
      } else {
        let rumnab = Math.floor(Math.random() * this.getplaymusicids.length);
        while (rumnab == this.playmusicindex) {
          rumnab = Math.floor(Math.random() * this.getplaymusicids.length);
        }
        this.playmusicindex = rumnab;
      }
      this.musicid = this.getplaymusicids[this.playmusicindex];
    },
    //查看播放列表
    getplaymusic() {
      this.show = true;
      //  this.setmusicids();
      this.getallmusicdata();
    },
    //获取所有歌曲
    getallmusicdata() {
      // 获取歌曲详情
      // // 验证通过
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "GET",
        url: "/song/detail",
        params: {
          ids: this.getplaymusicids.join(","),
        },
      })
        .then((result) => {
          this.$toast.clear();
          if (result.data.code === 200) {
            this.songListsData = result.data.songs;
          }
        })
        .catch((err) => {
          this.$toast.clear();
          console.error(err)
        });
    },
    //点击播放歌曲
    setStateCount(item) {
      this.$store.commit("setmusicid",item.id)
    },
    //获取歌曲评论
    getmusic(mid) {
      this.axios({
        method: "GET",
        // 获取歌单详情
        url: "/comment/music",
        params: {
          id: mid,
        },
      })
        .then((result) => {
          if (result.data.code === 200) {
            this.reviewscount = result.data.total;
          }
        })
        .catch((err) => {
          console.error(err)
        });
    },
    //播放歌单
    playAllMusic() {
      let musicData = {
        id: this.getplaymusicids[0],
      };
      this.$store.commit("setmusicid", musicData);
    },
  },
  components: {
    Lyric,
  },
};
</script>