import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const originalPush = VueRouter.prototype.push;
VueRouter.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err)
};

const routes = [
  {
    path: '/userLogin',
    name: 'UserLogin',
    component: () => import('../views/UserLogin.vue'),
    meta:{keepAlive:true}
  },
  {
    path: '/main',
    name: 'Main',
    component: () => import('../views/Main.vue'),
    children:[
      {
        //  首页
        path:'/home',
        name:'Home',
        // meta: { index: 1 },
        component: () => import('../views/mainViews/Home.vue')
       },
      {
        //  菜丹
        path:'/video',
        name:'Video',
        // meta: { index: 2 },
        component: () => import('../views/mainViews/Video.vue')
       },
       {
        //  购物袋
        path:'/recommend',
        name:'Recommend',
        // meta: { index: 3 },
        component: () => import('../views/mainViews/Recommend.vue')
       },
      {
        //  我的
        path:'/my',
        name:'My',
        // meta: { index: 4 },
        component: () => import('../views/mainViews/My.vue'),
        meta:{keepAlive:true}
       },
    ]
  },
  {
    path: '/forgot',
    name: 'Forgot',
    component: () => import('../views/Forgot.vue'),
  },
  {
    path: '/songlistdetails',
    name: 'SongListDetails',
    component: () => import('../views/SongListDetails.vue'),
  },
  {
    path: '/daily',
    name: 'Daily',
    component: () => import('../views/Daily.vue'),
  },
  {
    path: '/singerDetail',
    name: 'SingerDetail',
    component: () => import('../views/SingerDetail.vue'),
  },
  {
    path: '/singerAllMusic',
    name: 'SingerAllMusic',
    component: () => import('../views/SingerAllMusic.vue'),
  },
  {
    path: '/songAlbumDetails',
    name: 'SongAlbumDetails',
    component: () => import('../views/SongAlbumDetails.vue'),
  },
  {
    path: '/search',
    name: 'Search',
    component: () => import('../views/Search.vue'),
    children:[
      {
        //  首页
        path:'/searchHot',
        name:'SearchHot',
        component: () => import('../views/searchViews/SearchHot.vue')
       },
      {
        //  菜丹
        path:'/searchDetail',
        name:'SearchDetail',
        component: () => import('../views/searchViews/SearchDetail.vue')
       }
    ]
  },
  {
    path:"*",
    redirect: {
      name:"Home"
    }
  }
 
]

const router = new VueRouter({
  routes
})

export default router
