</template>
<script>
import PlayMusic from "./components/PlayMusic"
export default {
  data() {
    return {
      ismusicid:false
    }
  },
  computed: {
    getmusicid(){
      return this.$store.state.musicData
    },
  },
  watch: {
    getmusicid(val,oval){
      if (val != oval) {
        this.ismusicid = true
      }
    }
  },
  components: {
    PlayMusic
  }
  
}
</script>
